import { css } from 'lit';

export const styles = css`:host{color:var(--body-color);background:var(--body-background);border-radius:var(--border-radius)}[part=header]{background:var(--header-background)}[part=header]:focus,[part=header]:active{background:var(--header-focus-background)}[part=title]{color:var(--header-title-color)}[part=subtitle]{color:var(--header-description-color)}[part~=indicator]{color:var(--header-icon-color)}:host([disabled]) [part=title],:host([disabled]) [part=subtitle],:host([disabled]) [part~=indicator]{color:var(--disabled-text-color)}`;
